.profile__avatar {
  margin-right: 30px;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
}

@media screen and (max-width: 600px) {
  .profile__avatar {
    margin-right: 0;
    margin-bottom: 26px;
    align-self: center;
    width: 120px;
    height: 120px;
  }
}
